.project {
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0 4px 6px rgba(199, 14, 14, 0.1); /* Adding colored box shadow */
  transition: transform 0.2s linear;
  border: 2px solid yellow; /* Updated border property */
  border-radius: 12px;
}
  .project:hover {
    transform: translateY(-7px);
    box-shadow: 4px;
    box-shadow: 0 4px 6px rgba(199, 14, 14, 0.1); /* Adding colored box shadow */

  }
  
  .project__description {
    margin-top: 1em;
  }
  
  .project__stack {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 1.2em 0;
  }
  
  .project__stack-item {
    margin: 0.5em;
    font-weight: 500;
    font-size: 0.8rem;
    color: var(--clr-fg-alt);
  }
  
  .project .link--icon {
    margin-left: 0.5em;
  }
  .infotech {
    color: yellow;
  }
  